


































































































































.smart-modal-content {
  padding: 24px;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
}

.modal-header-icon {
  text-align: center;
  margin-bottom: 16px;
  flex-shrink: 0;
}

.modal-content {
  text-align: center;
  flex: 1;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.content-text {
  font-size: 16px;
  color: rgba(0, 0, 0, 0.85);
  margin-bottom: 16px;
  line-height: 1.5;
}

.content-footer {
  color: rgba(0, 0, 0, 0.45);
  font-size: 14px;
  margin-top: 16px;
  flex-shrink: 0;
}

.content-footer .anticon {
  margin-right: 8px;
}

.modal-footer {
  margin-top: 24px;
  text-align: center;
  padding-top: 16px;
  border-top: 1px solid #f0f0f0;
  flex-shrink: 0;
}

/* 自定义主题色 */
:deep(.ant-modal-header) {
  background: #f0f2f5;
  border-bottom: 1px solid #e8e8e8;
}

:deep(.ant-modal-title) {
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
}

:deep(.ant-modal-close) {
  color: rgba(0, 0, 0, 0.45);
}

:deep(.ant-modal-close:hover) {
  color: rgba(0, 0, 0, 0.75);
}

/* 滚动条样式 */
:deep(.ant-modal-body) {
  scrollbar-width: thin;
  scrollbar-color: rgba(0, 0, 0, 0.15) transparent;
}

:deep(.ant-modal-body::-webkit-scrollbar) {
  width: 6px;
  height: 6px;
}

:deep(.ant-modal-body::-webkit-scrollbar-thumb) {
  background-color: rgba(0, 0, 0, 0.15);
  border-radius: 3px;
}

:deep(.ant-modal-body::-webkit-scrollbar-track) {
  background-color: transparent;
}
